import React from "react";
import "../App.css";
import HeroItem from "./HeroItem";
//import heroes from '../assets/heroes';

const HeroList = ({ filteredOptions }) => {

  function singleHero(hero) {
    return <HeroItem hero={hero} key={hero.heroName} />;
  }


  return <div className="hero-list">
      {filteredOptions.map(singleHero)}
      </div>;
};

export default HeroList;
