import React, { useState, useEffect } from 'react';
import './App.css';
import HeroList from './components/HeroList';
import Form from './components/Form';
import Pagination from './components/Pagination'
import heroes from './assets/heroes';

const App = () => {
    const [heroFaction, setHeroFaction] = useState('all');
    const [heroType, setHeroType] = useState('both');
    const [heroGender, setHeroGender] = useState('both');
    const [filteredOptions, setFilteredOptions] = useState(heroes);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(6);

    useEffect(() => { 
        let temp = heroes
        if(heroFaction!=="all") temp = temp.filter(hero => hero.faction === heroFaction)
        if(heroType!=="both") temp = temp.filter(hero => hero.type === heroType)
        if(heroGender!=="both") temp = temp.filter(hero => hero.gender === heroGender)

        setFilteredOptions(temp)

    }, [heroFaction, heroType, heroGender])

    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredOptions.slice(indexOfFirstItem, indexOfLastItem);

    const paginate = pageNumber => setCurrentPage(pageNumber);
    /*const prevPage = () => setCurrentPage(currentPage - 1);
    const nextPage = () => setCurrentPage(currentPage + 1);*/

    return (
        <div className="app">
            <Form
                setHeroFaction={setHeroFaction}
                setHeroType={setHeroType}
                setHeroGender={setHeroGender}
            />
            <HeroList filteredOptions={currentItems} />
            <Pagination
                itemsPerPage={itemsPerPage}
                totalItems={filteredOptions.length}
                paginate={paginate}
            />
        </div>
    )
}

export default App;
