import React from "react";
import "../App.css";
import heroes from "../assets/heroes";

const Form = ({ setHeroFaction, setHeroType, setHeroGender }) => {
  function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
  }

  const getUniqueFactions = () => {
      // izvlacimo sve fakcije iz datoteke
      const fact = heroes.map((h) => h.faction);
      const factions = []
      // ubacujemo all vrednost u niz
      factions.push("all")
      // filtriramo samo jedinstvene vrednosti iz fakcija
      const unique = fact.filter(onlyUnique)
      // ubacujemo sve jedinstvene vrednosti u niz  ... <- razdajanjem niza        
      factions.push(...unique);
      // vracamo sve fakcije
      return factions;
  };
  

  const getUniqueTypes = () => {
      const oneType = heroes.map((h) => h.type);
      const types = []
      types.push("both")
      const unique = oneType.filter(onlyUnique)
      types.push(...unique);
    return types;
  };

  const getUniqueGenders = () => {
    const gen = heroes.map((h) => h.gender);
    const genders = []
    genders.push("both")
    const unique = gen.filter(onlyUnique)
    genders.push(...unique);
  return genders;
};

  const factions = getUniqueFactions();
  const types = getUniqueTypes();
  const genders = getUniqueGenders();

  function singleOption(val) {
    return (
      <option value={val}>
        {val.charAt(0).toUpperCase() + val.slice(1)}
      </option>
    );
  }

  return (
    <form className="form">
      <div className="select-item">
        <label>Faction:</label>
        <select
          onChange={(e) => setHeroFaction(e.target.value)}
          name="faction"
          className="dropdown"
        >
          {factions.map(singleOption)}
        </select>
      </div>

      <div className="select-item">
        <label>Type:</label>
        <select
          onChange={(e) => setHeroType(e.target.value)}
          name="type"
          className="dropdown"
        >
        {types.map(singleOption)}
        </select>
      </div>

      <div className="select-item">
        <label>Gender:</label>
        <select
          onChange={(e) => setHeroGender(e.target.value)}
          name="gender"
          className="dropdown"
        >
        {genders.map(singleOption)}
        </select>
      </div>
    </form>
  );
};

export default Form;
