import React from 'react';
import '../App.css';

import male from "../assets/man.jpg";
import female from "../assets/woman.jpg";

const HeroItem = (props) => {
    const {heroName, faction, type, gender, biography} = props.hero;

    var isMale = "";

    if (gender === "male") {
        isMale = true;
    } else {
        isMale = false;
    }

    let heroClass = "";
    let factionAndType = `${faction}-${type}`;

    switch(factionAndType) {
        case("haven-might"):
            heroClass = "Knight";
            break;
        case("haven-magic"):
            heroClass = "Cleric";
            break;
        case("sylvan-might"):
            heroClass = "Ranger";
            break;
        case("sylvan-magic"):
            heroClass = "Druid";
            break;
        case("academy-might"):
            heroClass = "Alchemist";
            break;
        case("academy-magic"):
            heroClass = "Wizard";
            break;
        case("fortress-might"):
            heroClass = "Thane";
            break;
        case("fortress-magic"):
            heroClass = "Rune Priest";
            break;
        case("bastion-might"):
            heroClass = "Barbarian";
            break;
        case("bastion-magic"):
            heroClass = "Seer";
            break;
        case("stronghold-might"):
            heroClass = "Warchief";
            break;
        case("stronghold-magic"):
            heroClass = "Shaman";
            break;
        case("sanctuary-might"):
            heroClass = "Samurai";
            break;
        case("sanctuary-magic"):
            heroClass = "Shaolin";
            break;
        case("dungeon-might"):
            heroClass = "Overlord";
            break;
        case("dungeon-magic"):
            heroClass = "Warlock";
            break;
        case("necropolis-might"):
            heroClass = "Death Knight";
            break;
        case("necropolis-magic"):
            heroClass = "Necromancer";
            break;
        case("inferno-might"):
            heroClass = "Demoniac";
            break;
        case("inferno-magic"):
            heroClass = "Heretic";
            break;
        default:
            heroClass = "Hero";
    }

    let factionColor = "";

    switch(faction) {
        case("haven"):
            factionColor = {
                backgroundColor: "#4f85db"
            };
        break;
        case("inferno"):
            factionColor = {
                backgroundColor: "#b20000"
            };
        break;
        case("necropolis"):
            factionColor = {
                backgroundColor: "#10641b",
                color: "#FFF"
            };
        break;
        case("sylvan"):
            factionColor = {
                backgroundColor: "#66BB33"
            };
        break;
        case("dungeon"):
            factionColor = {
                backgroundColor: "#7659ab",
                color: "#FFF"
            };
        break;
        case("academy"):
            factionColor = {
                backgroundColor: "#ffcc66"
            };
        break;
        case("fortress"):
            factionColor = {
                backgroundColor: "#e06230"
            };
        break;
        case("stronghold"):
            factionColor = {
                backgroundColor: "#663e27",
                color: "#FFF"
            };
        break;
        case("sanctuary"):
            factionColor = {
                backgroundColor: "#a2d7d4"
            };
        break;
        case("bastion"):
            factionColor = {
                backgroundColor: "#E6EDF2"
            };
        break;
        default:
            factionColor = {
                backgroundColor: ""
            };
    }

    /*const imgStyle = {
        width: "250px"
    }

    const alignCenter = {
        textAlign: "left"
    }*/

    return (
        <div className="hero-item">
            <div className="hero-pic">
                {isMale && <img src={male} alt="male" />}
                {!isMale && <img src={female} alt="female" />}
            </div>
            <div className="hero-name" style={factionColor}>
                <h1>{heroName} the {heroClass}</h1>
            </div>
            <div className="hero-bio">
                <p>{biography}</p>
            </div>
        </div>
    )
}

export default HeroItem;